<template>
    <div>
      <loading-vue v-show="musicList.length==0"></loading-vue>
       <ul  class="showMu">
          <li v-for="(key,index) in musicList" @click="$store.commit('hash',index)"><span>{{index+1}}</span>{{key.filename}}</li>
       </ul>
       <div class="jiazai" @click="jiazai">加载更多...</div>
       <play-vue v-show="hashM!=''"></play-vue>
    </div>
</template>
<script>
  import PlayVue from "./Play.vue"
  import {mapState} from "vuex";
    export default {
    	methods:{
    		jiazai:function(){
    			this.$http.get("http://www.wbshow.top/kugou.php", {
		              params:{
		                page:this.singPage
		              }
		            }).then(function(res) {
		              console.log(res.data.songs.list)
		            this.$store.commit("addSongs",res.data.songs.list)
		            }.bind(this))
    		}
    	},
      components:{
        "play-vue": PlayVue
      },
        mounted: function() {
            this.$http.get("http://www.wbshow.top/kugou.php", {
              params:{
                page:1
              }
            }).then(function(res) {
              console.log(res.data.songs.list)
            this.$store.commit("addList",res.data.songs.list)
            }.bind(this))
        },
        computed:{
          ...mapState([
            "musicList",
            "hashM",
            "singPage"
          ])
        }
    }
</script>


