<template>
<div class="playmusic">
	 
	<div class="imgs">
		<img :src="urlM" alt="">
	</div>
	<div class="sz_center">
		<div class="sz_singname">
			<b>{{songName}}</b>
			<h1>{{singerName}}</h1>
		</div>		
		<div class="btns">			
			<i class="btn prev iconfont" @click="last">&#xe64d;</i>
			<i class="btn play iconfont"  id="play" @click="pause">&#xf0067;</i>
			<i class="btn next iconfont" @click="next">&#xe64e;</i>		
		</div>
	</div>	
   <audio  id="au" controls :src="musicM"  style="display:none" autoplay></audio>  
</div>
</template>
<script>
import {mapState} from "vuex"
export default{
    methods:{   	
        pause(){
            var Ms=document.getElementById("au");
            var Play=document.getElementById("play");
            if(!Ms.paused){
            	
            	Ms.pause();
            	Play.innerHTML='&#xf004c;'
            }else{
            	Ms.play();
            	Play.innerHTML='&#xf0067;'
            	
            }
            
        },
        next(){
        	if(this.singIndex>=this.musicList.length-1){
        		
        	}else{
        		this.$store.commit("hash",this.singIndex);
        	}
        	
        },
        last(state,index){
        	if(this.singIndex1<=0){
        		
        	}else{
        		this.$store.commit("hash",this.singIndex1); 
        	}
        }
    },
    mounted:function(){
    	var _this=this;
    	console.log(_this)
    	var Au=document.getElementById("au");
    	Au.addEventListener("ended",function(){
    		_this.$store.commit("hash",_this.singIndex);
			Au.play();
    	})
    },
    watch:{
       hashM:function(){
           this.$http.get("http://www.wbshow.top/music.php",{
             params:{
                   hash: this.hashM
             }
           }).then(function (res){
               console.log(res.data)
               this.$store.commit("addImg",res.data.imgUrl);
               this.$store.commit("addMusic", res.data.url);
               this.$store.commit("title",res.data.songName);
               this.$store.commit("addsinger",res.data.singerName)
           }.bind(this))
       
       }
    },
    computed:mapState([
       "hashM",
       "urlM",
       "musicM",
       "songName",
       "singerName",
       "singIndex",
       "singIndex1" ,
       "musicList"
    ])
}
</script>


