import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
//scss bootstrop vue vuex 矢量图
//Sass也通过另一种强大的方式帮你组织文件——使用局部系统把你的样式表分成不同的部分，如页眉或页脚，样式表会立刻变得更高效

//1.轻量级的框架
//
//2.双向数据绑定
//
//3.指令
//
//4.插件化
//它采用集中式存储管理应用的所有组件的状态，并以相应的规则保证状态以一种可预测的方式发生变化
//vuex 就是使用一个 store 对象来包含所有的应用层级状态，也就是数据的来源。当然如果应用比较庞大，我们可以将 store 模块化，也就是每个模块都有自己的 store。
var state={
    title:"排行榜单",
    musicList:[],
    hashM:"",
    urlM:"",
    musicM:"",
    songName:"",
    singerName:"",
    singIndex:null,
    singIndex1:null,
    singPage:2
}
var mutations={
    change(state,newTitle){
        state.title = newTitle
    },
    addList(state,arr){
        state.musicList = arr;
    },
    addSongs(state,arr){
        state.musicList = state.musicList.concat(arr);
        state.singPage=state.singPage+1
    },
    hash(state,index){
        state.hashM=state.musicList[index].hash; 
        state.singIndex=index+1
        state.singIndex1=index-1
        //console.log(state.hashM);
    },
    addImg(state,url){
        state.urlM=url.replace("{size}/","")
        console.log(state.urlM)
    },
    addMusic(state,url){
        state.musicM=url
    },
    title(state,url){
        state.songName=url;
    },
    addsinger(state,url){
        state.singerName=url;
    }
}

var store= new Vuex.Store({
    state,
    mutations
})
export default store


