<template>
    <header>
        {{title}}
    </header>
</template>
<script>
import {mapState} from "vuex";
    export default{
        computed:mapState([
            "title"
        ]),
        watch:{
            '$route' (to, from) {
               if(to.path=="/top"){
                   this.$store.commit("change","排行榜单")
               }else if(to.path=="/my"){
                   this.$store.commit("change","我的")
               }else{
                   this.$store.commit("change","搜索")
               }
            }
        }
    }
</script>


